}
.m-section__header,.m-section__navigation{position:relative;overflow:hidden
}
.m-section__header{min-height:24rem;background-color:#1a252f
}
.m-section__header.s-background--transparent{background:transparent;min-height:36rem;z-index:1111 !important
}
.m-section__navigation .e-slogan{margin-left:16px
}
.user__name:hover .user__menu{display:block
}
.user__menu{position:absolute;top:5%;padding:0 1rem;border:1px solid #39b6eb;border-radius:4px;display:none
}
.user__menu ul{list-style:none;margin:0;padding:0
}
.user__menu li{margin:0.8rem 0
