.m-link__card{text-align:center;color:#ededed;display:inline-block;overflow:hidden;width:100%
}
.m-link__card .e-description{font-size:1.22rem
}
.m-link__card:hover .muzikdj-card{border:1px solid #39b6eb
}
.m-link__card:hover .e-description{color:#39b6eb
}
.muzikdj-card{background-color:rgba(0,0,0,0.3)
}
.muzikdj-card.mdl-card{width:100%;min-height:auto;height:auto;padding-bottom:100%;cursor:pointer;margin-left:auto;margin-right:auto;margin-bottom:0.5rem
}
.listen-rooms-card{width:90%;height:auto;margin-left:auto;margin-right:auto;background-image:none;background-position:center center;background-repeat:no-repeat;background-color:white;-ms-background-size:cover;background-size:cover
}
.listen-rooms-card .mdl-card__actions{background-color:rgba(0,0,0,0.66)
}
.listen-rooms-card .mdl-card__title{-ms-flex-pack:justify;justify-content:flex-end;padding:20.5% 16px;background-color:rgba(255,255,255,0.36)
