.room-playlist__table{background:transparent;width:100%;margin:0 auto
}
.room-playlist__table th,.room-playlist__table td{font-size:1rem;vertical-align:middle
}
.room-playlist__table th{font-size:1.226rem
}
.room-playlist__table tbody tr:hover{background:rgba(57,182,235,0.3)
}
.room-playlist__table tbody tr.playing{border-left:2px solid #39b6eb
}
.room-playlist__table .song{cursor:pointer
}
.room-playlist__table .song-cover{width:10rem;text-align:center
}
.room-playlist__table td.song-cover{padding-bottom:7.5rem;background-color:#333333;background-repeat:no-repeat;background-position:center center;background-image:none;-ms-background-size:cover;background-size:cover;position:relative;overflow:hidden
}
.room-playlist__table td.song-cover .icon--floating{position:absolute;font-size:5rem;top:1.75rem;left:calc(50% - 2.5rem);opacity:0.5
}
.room-playlist__table .song-title{width:calc(100% - 27rem)
}
.room-playlist__table .song-dj{width:10rem
}
.room-playlist__table .song-remove{width:2rem
}
.video-player--masker::after{content:"";display:block;position:absolute;top:0;left:0;right:0;bottom:0;background:url(/assets/images/dot_masker.png) left bottom repeat rgba(0,0,0,0.36);z-index:1000
}



